	module.exports = {"data":{"markdownRemark":{"html":"<p>While transitioning our main website to React &#x26; creating our design system at <a href=\"https://www.lyst.com\">Lyst</a>, I continuously kept hearing some variation of \"we should setup Yeoman to make new components/containers/etc.\". Maybe because <a href=\"http://yeoman.io/\">Yeoman</a> is quite complex to initially set-up or perhaps just because it's <strong>so</strong> powerful that the docs can be quite intimidating: nothing. ever. happened.</p>\n<p>So...</p>\n<h2>I built a thing üç™ ‚úÇÔ∏è</h2>\n<p>Cookiecutter is a CLI for creating boilerplate files/folders based on templates you provide. It's kind of like <a href=\"https://github.com/audreyr/cookiecutter\">Python's Cookiecutter</a> but much, much simpler.</p>\n<p>you can install via npm: <code>npm install cookiecutter</code></p>\n<p>Check out the <a href=\"https://github.com/mattvagni/cookiecutter\">docs on github</a>.</p>\n<figure class=\"medium\">\n  <img src=\"/cookiecutter-example-3d642c9625963ecd2edf94b3b865e8fd.gif\" alt=\"Cookiecutter example\">\n  <figcaption>\n    Example from one of our repos\n  </figcaption>\n</figure>\n<p>üëç Cookiecutter supports:</p>\n<ul>\n<li>Multiple templates with the ability to pick which template to use via the CLI.</li>\n<li>Multiple fields per template with the ability to specify them from the CLI.</li>\n<li>Custom validation &#x26; error messages for fields.</li>\n<li>Any type of file or folder structure.</li>\n</ul>\n<p>üëé Cookiecutter <strong>doesn't</strong> support:</p>\n<ul>\n<li>Conditionals or any other logic in templates. (You can however achieve similar things by having multiple templates).</li>\n<li>Bootstrapping a project from scratch (i.e \"I have no code but need to set a project up\").</li>\n<li>Adding code to existing files.</li>\n<li>Lots of other things Yeoman does support. Cookiecutter is really quite simple &#x26; isn't suited for complex use-cases.</li>\n</ul>\n<hr>\n<h2>Try it out</h2>\n<p>Find out more about <a href=\"https://github.com/mattvagni/cookiecutter\">how to configure cookiecutter etc. on Github</a>.</p>\n<h2>Any feedback?</h2>\n<p>Would love to know what you think. Just <a href=\"https://github.com/mattvagni/cookiecutter/issues/new\">open an issue</a> or find me on <a href=\"https://www.twitter.com/mattvagni\">twitter</a>.</p>","frontmatter":{"title":"npm install cookiecutter","date":"28 May, 17"}}},"pathContext":{"path":"/posts/cookiecutter/"}}

